  #opy5 {
    opacity: 0;
    transition: opacity 0.7s ease-in-out;
  }
  .l-text {
    top: 40%;
    width: 60vw;
    height: 238px;
    left: 20px;
    top: 35%;
  }
  .svgpos {
    padding-top: 50px;
    padding-left: 50px;
  }
  li {
    z-index: 16;
    color: black !important;
  }
  #f-rt {
    padding-top: 100px;
    padding-bottom: 50px;
    text-align: end;
    padding-right: 20vw;
    display: none;
  }
  .f-rt ul li {
    padding-top: 30px;
    margin: auto;
  }
  .f-r {
    display: none;
  }
  .toggler {
    display: block;
    padding-right: 30px;
  }
  nav {
    justify-content: space-between;
  }
  .l-text {
    top: 40%;
    height: 267px;
    left: 0px;
    margin-left: 15px;
    /* top: 275px; */
    /* font-family: Text Me One; */
    /* font-style: normal; */
    /* font-weight: normal; */
    font-size: 48px;
    /* line-height: 124.1%; */
    /* letter-spacing: 0.16em; */
    color: #ffffff;
  }
  .component_container {
    opacity: 0.5;
  }

  .tam_component_col {
    width: 50%;
  }
  .tam_component {
    position: relative;
    padding: 10px;
    margin-top: -10vw;
    display: flex;
    justify-content: space-between;
    z-index: 29;
  }
}

.card-container {
  display: inline-block;
  position: relative;
  width: 100%;
}
img {
  opacity: 1;
  display: block;
  width: 100%;
  transition: 0.5s ease;
  backface-visibility: hidden;
}

.hoverText {
  position: absolute;
  top: 0px;
  height: 100%;
  transition: 0.5s ease;
  opacity: 1;
  width: 100%;
  text-align: right;
}

.caption {
  color: white;
  font-size: 30px;
  border-radius: 6px;
  height: 100%;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  font-weight: bolder;
}
.hrt-ico {
  font-size: 30px;
  animation: popheart 0.5s cubic-bezier(0.36, 0.07, 0.57, 0.99);
  padding: 1.2rem;
  transition: color 0.2s cubic-bezier(0, 0, 0.2, 1);
  cursor: pointer;
}
.hrt-ico:hover {
  color: red;
  /* background-color: red; */
  font-size: 30px;
  font-weight: auto;
}
.hrt-ico-hover {
  color: red;
  /* background-color: red; */
  font-size: 30px;
  font-weight: 600 !important;
  padding: 1.2rem;
  animation: popheart 0.5s cubic-bezier(0.36, 0.07, 0.57, 0.99);
  cursor: pointer;
}
.hrt-ico:focus {
  color: red;
  /* background-color: red; */
  font-size: 30px;
  font-weight: 600;
}
@keyframes popheart {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(5);
  }
  100% {
    transform: scale(1);
  }
}

.over_pop {
  background: #0a0a0a94;
  display: flex;
  height: 100vh;
  width: 100%;
  position: fixed;
  z-index: 51;
  justify-content: center;
  align-items: center;
}

.ip_cont {
  height: 100%;
  max-width: 200px;
  max-height: 100px;
  background: white;
  border-radius: 14px;
  width: 100%;
  transform: scale(1.8);
}

.ip_cont input {
  margin-left: auto;
  margin-right: auto;
  display: block;
  margin-top: 40px;
  border: none;
  border-bottom: 3px solid blue;
}
