@import url(https://fonts.googleapis.com/css2?family=Text+Me+One&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
  font-family: "Text Me One", sans-serif;
}
* {
  box-sizing: border-box;
}

/*NAV*/
.overlay {
  display: block;
  height: 100%;

  position: fixed;
  z-index: 31;
  top: 0;
  left: 0;
  /* background-color: rgb(0, 0, 0); */
  background-color: rgb(255 255 255);
  overflow-x: hidden;
  transition: 0.5s;
}
.overlayclose {
  width: 0;
}
.overlayopen {
  width: 100%;
}
.overlay-content {
  position: relative;
  top: 25%;
  width: 100%;
  text-align: center;
  margin-top: 30px;
}

.overlay a {
  text-align: right;

  padding: 8px;
  padding-right: 5%;
  text-decoration: none;
  font-size: 36px;
  color: #818181;
  display: block;
  transition: 0.3s;
}

.overlay a:hover,
.overlay a:focus {
  color: #f1f1f1;
}

.overlay .closebtn {
  position: absolute;
  top: 20px;
  right: 45px;
  font-size: 60px;
}

@media screen and (max-height: 450px) {
  .overlay a {
    font-size: 20px;
  }
  .overlay .closebtn {
    font-size: 40px;
    top: 15px;
    right: 35px;
  }
}
.landing {
  position: relative;
  z-index: 1;
  width: 100%;
  height: 100vh;
  left: 0px;
  background: linear-gradient(
    180deg,
    rgba(20, 0, 255, 0.82) 0%,
    #4c30fe 43.23%,
    rgba(255, 135, 135, 0.54) 82.81%,
    #ffffff 100%
  );
}

nav {
  position: fixed;
  z-index: 30;
  background: #3f2eff;
  margin: auto;
  width: 100%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  padding-top: 3%;
  padding-bottom: 15px;
}
.logo {
  width: 30%;
}
.logo img {
  display: flex;
  width: auto;
}
.f-r {
  float: right;
  width: 60%;
}
.f-r ul {
  justify-content: flex-end;
  display: flex;
  align-self: flex-end;
  padding-top: 5%;
}
li {
  padding-left: 45px;
  height: 45px;
  left: 853px;
  font-size: 24px;
  line-height: 124.1%;
  text-align: center;
  letter-spacing: 0.16em;
  color: white !important;
  list-style: none !important;
  border-bottom: 3px solid rgba(255, 255, 255, 0);
  transition: all 0.3s;
  transition-timing-function: ease-in-out;
}
.toggler {
  display: none;
  cursor: pointer;
}
a {
  cursor: pointer;
  text-decoration: none !important;
}

li:hover {
  font-weight: 500;
  border-bottom: 3px solid white;
}

.m-dropdown {
  display: none;
}

a {
  color: var(--bs-white);
  text-decoration: underline;
}
a:hover {
  color: var(--bs-white);
}
.move_down {
  padding-top: 15%;
}
.l-text {
  position: absolute;
  width: 80vw;
  height: auto;
  left: 92px;
  top: 40%;
  font-style: normal;
  font-weight: normal;
  font-size: 96px;
  line-height: 124.1%;
  letter-spacing: 0.16em;
  color: #ffffff;
}

.footer {
  background-image: linear-gradient(
    180deg,
    #ffffff 0%,
    #febdbe 37.71%,
    rgba(114, 82, 238, 0.78) 56%,
    rgba(65, 45, 255, 0.9) 97%
  );
}

.ip {
  border: 3px solid #4c30fe;
  box-sizing: border-box;
  border-radius: 14px;
  background-color: #fff0;
}
.form-control:focus {
  color: #212529;
  background-color: #fff0;
  border-color: #4c30fe;
  outline: 0;
  box-shadow: 0 0 0 1px #aa76ff;
}
.component_container {
  margin-top: -90vh;
  display: flex;
  opacity: 0.2;
  justify-content: space-between;
}
.component_row {
  display: flex;
  flex-wrap: wrap;
}
.component_col {
  width: 25%;
}
.component_col_bg {
  width: 33.33%;
}
.component_col_bg img {
  width: 98%;
  padding: 1%;
}
.tam_component {
  position: relative;
  padding: 10px;
  margin-top: -6vw;
  display: flex;
  justify-content: space-between;
  z-index: 29;
}
.tam_component_row {
  display: flex;
  flex-wrap: wrap;
}
.tam_component_col {
  width: 25%;
}
.tam_component_col img {
  border-radius: 14px;
  width: 98%;
  padding: 1%;
}

.bg-about {
  height: auto;
  background: linear-gradient(
    180deg,
    rgba(252, 160, 0, 0.77) 0%,
    rgba(255, 255, 255, 0.82) 34.59%,
    rgba(255, 255, 255, 0.76) 51.85%,
    rgb(252 160 0) 100%
  );
}
@media only screen and (max-width: 1000px) {
  .l-text {
    width: 80vw;
    left: 60px;
    font-size: 50px;
  }
}
@media only screen and (max-width: 768px) {
  .tam_component {
    position: relative;
    padding: 10px;
    margin-top: -10vw;
    display: flex;
    justify-content: space-between;
    z-index: 29;
  }
  nav {
    position: absolute;
    width: 100vw;
    position: fixed;
    background: #402fff;
    justify-content: space-between;
  }
  .fa-bars {
    color: white;
    font-size: 50px !important;
  }
  .logo {
    width: 30%;
    padding-left: 50px;
  }
  .logo img {
    display: flex;
    width: 60%;
  }
  .m-dropdown {
    display: block;
    width: 0%;
    position: relative;
    border-radius: 0px 156px 84px 200px;
    height: 600px;
    z-index: 15;
    background: #ffffff;
    transition: width 0.5s ease-in-out;
  }

  #opy1,
  #opy2,
  #opy3,
  #opy4,
  #opy5 {
    cursor: pointer;
    opacity: 0;
    transition: opacity 0.7s ease-in-out;
  }
  .svgpos {
    padding-top: 50px;
    padding-left: 50px;
  }
  li {
    z-index: 16;
    color: black !important;
  }
  #f-rt {
    padding-top: 100px;
    padding-bottom: 50px;
    text-align: end;
    padding-right: 20vw;
    display: none;
  }
  .f-rt ul li {
    padding-top: 30px;
    margin: auto;
  }
  .f-r {
    display: none;
  }
  .toggler {
    display: block;
    padding-right: 50px;
  }

  .l-text {
    top: 40%;
    width: 80vw;
    left: 50px;
    font-size: 50px;
  }
  .component_container {
    opacity: 0.5;
    height: 100vh !important;
    margin-top: -100vh;
    display: flex;
    justify-content: space-between;
  }
  .component_row {
    display: flex;
    flex-wrap: inherit;
    flex-direction: column-reverse;
  }
  .f-img {
    opacity: 0.5;
  }

  .tam_component_col {
    width: 50%;
  }
}
@media only screen and (max-width: 425px) {
  nav {
    justify-content: space-between;
    position: fixed;
    padding-left: 15px;
  }
  .logo {
    width: 30%;
    padding-left: 0px;
  }
  .logo img {
    width: 70%;
    display: flex;
    height: 70%;
  }
  .fa-bars {
    font-size: 30px !important;
  }
  .m-dropdown {
    display: block;
    width: 0%;
    position: relative;
    border-radius: 0px 156px 84px 200px;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    z-index: 15;
    background: #ffffff;
    transition: width 0.5s ease-in-out;
  }
  #opy1,
  #opy2,
  #opy3,
  #opy4,
  #opy5 {
    opacity: 0;
    transition: opacity 0.7s ease-in-out;
  }
  .l-text {
    top: 40%;
    width: 60vw;
    height: 238px;
    left: 20px;
    top: 35%;
  }
  .svgpos {
    padding-top: 50px;
    padding-left: 50px;
  }
  li {
    z-index: 16;
    color: black !important;
  }
  #f-rt {
    padding-top: 100px;
    padding-bottom: 50px;
    text-align: end;
    padding-right: 20vw;
    display: none;
  }
  .f-rt ul li {
    padding-top: 30px;
    margin: auto;
  }
  .f-r {
    display: none;
  }
  .toggler {
    display: block;
    padding-right: 30px;
  }
  nav {
    justify-content: space-between;
  }
  .l-text {
    top: 40%;
    height: 267px;
    left: 0px;
    margin-left: 15px;
    /* top: 275px; */
    /* font-family: Text Me One; */
    /* font-style: normal; */
    /* font-weight: normal; */
    font-size: 48px;
    /* line-height: 124.1%; */
    /* letter-spacing: 0.16em; */
    color: #ffffff;
  }
  .component_container {
    opacity: 0.5;
  }

  .tam_component_col {
    width: 50%;
  }
  .tam_component {
    position: relative;
    padding: 10px;
    margin-top: -10vw;
    display: flex;
    justify-content: space-between;
    z-index: 29;
  }
}

.card-container {
  display: inline-block;
  position: relative;
  width: 100%;
}
img {
  opacity: 1;
  display: block;
  width: 100%;
  transition: 0.5s ease;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
}

.hoverText {
  position: absolute;
  top: 0px;
  height: 100%;
  transition: 0.5s ease;
  opacity: 1;
  width: 100%;
  text-align: right;
}

.caption {
  color: white;
  font-size: 30px;
  border-radius: 6px;
  height: 100%;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  font-weight: bolder;
}
.hrt-ico {
  font-size: 30px;
  -webkit-animation: popheart 0.5s cubic-bezier(0.36, 0.07, 0.57, 0.99);
          animation: popheart 0.5s cubic-bezier(0.36, 0.07, 0.57, 0.99);
  padding: 1.2rem;
  transition: color 0.2s cubic-bezier(0, 0, 0.2, 1);
  cursor: pointer;
}
.hrt-ico:hover {
  color: red;
  /* background-color: red; */
  font-size: 30px;
  font-weight: auto;
}
.hrt-ico-hover {
  color: red;
  /* background-color: red; */
  font-size: 30px;
  font-weight: 600 !important;
  padding: 1.2rem;
  -webkit-animation: popheart 0.5s cubic-bezier(0.36, 0.07, 0.57, 0.99);
          animation: popheart 0.5s cubic-bezier(0.36, 0.07, 0.57, 0.99);
  cursor: pointer;
}
.hrt-ico:focus {
  color: red;
  /* background-color: red; */
  font-size: 30px;
  font-weight: 600;
}
@-webkit-keyframes popheart {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(5);
  }
  100% {
    transform: scale(1);
  }
}
@keyframes popheart {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(5);
  }
  100% {
    transform: scale(1);
  }
}

.over_pop {
  background: #0a0a0a94;
  display: flex;
  height: 100vh;
  width: 100%;
  position: fixed;
  z-index: 51;
  justify-content: center;
  align-items: center;
}

.ip_cont {
  height: 100%;
  max-width: 200px;
  max-height: 100px;
  background: white;
  border-radius: 14px;
  width: 100%;
  transform: scale(1.8);
}

.ip_cont input {
  margin-left: auto;
  margin-right: auto;
  display: block;
  margin-top: 40px;
  border: none;
  border-bottom: 3px solid blue;
}

